import { pipeline, env } from "@xenova/transformers";
//  CRA was deprecated while we use create-rect-app so we get html from tokenizer so we use below settings
env.allowLocalModels = false;
env.useBrowserCache = false;

class MyTranslationPipeline {
  static task = "translation";
  static model = "Xenova/nlb-200-distilled-600M";
  static instance = null;

  static async getInstance(progress_callback = null) {
    if (this.instance === null) {
      this.instance = await pipeline(this.task, null, { progress_callback });
    }

    return this.instance;
  }
}
/* eslint-disable-next-line no-restricted-globals */
self.addEventListener('message',async(event)=>{
    let  translator = await MyTranslationPipeline.getInstance(
        x=>{
            /* eslint-disable-next-line no-restricted-globals */
            self.postMessage(x)
        }
    )

    let output= await translator(event.data.text,{
        tgt_lang:event.data.tgt_lang,
        src_lang:event.data.src_lang,
        callback_function: s=>{
            /* eslint-disable-next-line no-restricted-globals */
            self.postMessage({
                status:'update',
                output:translator.tokenizer.decode(s[0].output_token_ids,{
                    skip_special_tokens:true
                })
            })
        }
    })

    /* eslint-disable-next-line no-restricted-globals */
    self.postMessage({
        status:'complete',
        output
    })
})